.door-authorized {
  position: relative;
  margin: 50px auto;
  height: 200px;
  width: 200px; }
  .door-authorized.unauthorized {
    border: 2px solid #EEEEEE; }
  .door-authorized .unlock-spinner {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    border-radius: 50%;
    opacity: 0;
    -webkit-transition: opacity .5s ease;
    -o-transition: opacity .5s ease;
    transition: opacity .5s ease; }
  .door-authorized.unlocking .unlock-spinner, .door-authorized.unlocked .unlock-spinner, .door-authorized.unlock_failed .unlock-spinner {
    opacity: 1; }
  .door-authorized.unlocking .unlock-spinner {
    -webkit-animation: unlockSpin .75s linear 0s infinite;
            animation: unlockSpin .75s linear 0s infinite; }
  .door-authorized .unlock-icon__locked {
    margin: 50px; }
  .door-authorized .unlock-icon__complete {
    display: block;
    padding: 38px; }
    .door-authorized .unlock-icon__complete .unlock-icon__complete--path {
      stroke-dasharray: 1000;
      stroke-dashoffset: -100;
      -webkit-animation: completeCheck .9s ease-in-out forwards;
              animation: completeCheck .9s ease-in-out forwards; }
  .door-authorized .door-authorized__text {
    padding-top: 15px;
    font-size: 0.8em;
    text-align: center;
    font-weight: 300; }

@-webkit-keyframes unlockSpin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes unlockSpin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@-webkit-keyframes completeCheck {
  from {
    stroke-dashoffset: -100; }
  to {
    stroke-dashoffset: 900; } }

@keyframes completeCheck {
  from {
    stroke-dashoffset: -100; }
  to {
    stroke-dashoffset: 900; } }



