/* GLOBAL ELEMENT AND TYPOGRAPHY CSS STYLING */
h1, h2, h3, h4, h5, h6, p, span {
  font-family: 'Roboto', Helvetica, Arial, sans-serif; }

h2 {
  font-size: 1.8em;
  font-weight: 300; }

h3 {
  font-size: 1.3em; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

a {
  text-decoration: none;
  color: inherit; }

.text-center {
  text-align: center; }

.button-text {
  line-height: 36px;
  text-align: center; }

#App {
  padding-top: 64px; }



